import { validateGroup, message } from "../config/validate.config";
const mapEnglishToChinese = {
    phoneNumber: '',
    major: '',
    email: '',
    ps: '',
    validateCode: '',
    yourName: '',
    commentSingle: '',
    commentValidate: '',
    emailForStatus: '',
};
const isEmpty = (target) => {
    return target.value === 'undefined' || target.value === null || target.value === '' ? `${mapEnglishToChinese[target.id]}` : message.pass;
};
const validate = (target) => {
    const pattern = validateGroup[target.id];
    if(target.id !== 'ps') {
        let result = isEmpty(target);
        if (result !== message.pass) {
            return result;
        }
    }else {
        return pattern.test(target.value) ? message.pass : `${ mapEnglishToChinese[target.id] }80`;
    }
    return pattern.test(target.value) ? message.pass : `${ mapEnglishToChinese[target.id] }`;
};

export {
    validate
}